.goBack_button {
  align-items: center;
  justify-content: center;
  line-height: 1;
  text-decoration: none;
  color: #613535;
  font-size: 18px;
  border-radius: 20px;
  width: 200px;
  height: 40px;
  font-weight: bold;
  border: 2px solid #555555;
  transition: 0.3s;
  box-shadow: 0px 6px 0px -2px rgba(67, 145, 209, 1);
  background-color: #a5d3f8;
  margin: 20px;
}

.goBack_button:hover {
  box-shadow: 0 0 #fff;
  transform: translateY(1px);
}

.cast_container {
  text-align: center;
}

h2 {
  margin-top: 20px;
}

ul {
  list-style: none;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

li {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 20px;
}

img {
  max-width: 150px;
  margin-bottom: 10px;
}
